.card{
    display: flex;
    flex-direction: column;
    max-width: 25rem;
    border: 1px solid red;
    border-radius: 1.5rem;
    padding: 2rem;
    box-sizing: border-box;
    gap: 1.5rem;
    background-color: #ffffff14;
}

img{
    width: 100%;
    border-radius: 1.5rem;
}
.text_desc{
    color: #ffffff;
    font-family: "Outfit", sans-serif;;
    font-weight: 400;
}
